* {
  background-color: black;
}

.main {
  position: relative;
}

.main img{
  width: 100%;
  margin: 0 auto;
}

.form {
  display: flex;
  width: 130px;
  position: absolute;
  height: 5px;
  padding: 10px;
  top: 40%;
  left: 88%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  align-items: center;
}

.main input {
  width: 50px;
  height: 5px;
  padding: 10px;
  background: black;
  color: white;
  border: 0;
  border-radius: 10px;
  margin-right: 5px;
}

.main button {
  width: 20px;
  height: 5px;
  padding: 10px;
  background: black;
  color: white;
  border: 0;
  border-radius: 10px;
  display: flex;
  align-items: center;
  font-size: 5px;
  display: flex;
  justify-content: center;
}